.header {
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 0 auto 40px auto;
  width: 100%;
  align-items: center;
  transition: all .3s ease-in-out;
  box-sizing: border-box;
}

@media screen and (max-width: 768px) {
  .header {
    margin: 0 0 45px 0;
  }
}