.image-popup {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, .9);
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  transition: visibility .3s linear, opacity 0.3s linear;
}

