.element__like {
  background-image: url(../../../images/like.svg);
  background-color: #fff;
  background-size: cover;
  opacity: 1;
  border: 0;
  width: 22px;
  height: 19px;
  padding: 0;
  transition: all .3s ease-in-out;
}

.element__like:hover {
  opacity: .5;
  cursor: pointer;
}